.book-search-container {
    font-family: Arial, sans-serif;
  }


  .bookbg{
    background: url('../Images/man-1853545_1280.jpg') no-repeat center center;
    background-size: cover;
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;
    background-position: bottom;     
  }
  
  
  .search-bar {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;

  }

  .searchinputcon{
    display: flex;
  }
  
  .search-input {
    padding: 10px;
    width: 600px;
    border: none;
    border-radius: 4px;
    height: 50px;
    outline: none;
  }
  
  .search-input.error {
    border-color: red;
  }
  
  .search-button {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 61px;
    margin-left: 10px;
    border: none;
    border-radius: 4px;
    background-color: #252563;
    color: white;
    cursor: pointer;
    height: 50px;
}

  
  .search-button:disabled {
    background-color: #008bc4;
    cursor: not-allowed;
  }

  
  
  .error-message {
    color: red;
    text-align: center;
    margin-top: 10px;
  }
  
  .loading {
    text-align: center;
    font-size: 24px;
    margin-top: 20px;
  }
  
  .books-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .book-card {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 10px;
    width: 22%;
    text-align: center;
  }
  
  .book-card h3 {
    font-size: 18px;
  }
  
  .book-card p {
    font-size: 16px;
    color: #555;
  }
  
  .book-image {
    max-width: 100%;
    height: auto;
    margin-top: 10px;
  }
  
  .book-link {
    display: block;
    margin-top: 10px;
    color: #007bff;
    text-decoration: none;
  }


