* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
}

.Header-container {
  width: 100%;
}

.Header-container header {
  margin: auto;
  max-width: 85rem;
}

.Header-container header div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

header div nav {
  display: inline-block;
}

header div nav ul {
  display: inline-block;
}

header div nav ul li {
  list-style: none;
  display: inline-block;
  vertical-align: baseline;
}

header div nav ul li a {
  font-size: 17px;
  color: #8c929c;
  display: block;
  margin-left: 20px;
  margin-right: 20px;
  text-decoration: none;
  font-style: normal;
  letter-spacing: 1px;
  font-weight: 300;
  font-family: 'BebasNeue', sans-serif;
}

header div nav ul li a:hover {
  text-decoration: underline;
  text-underline-offset: 15px; /* Adjust this value to set the desired gap */
  color: navy;
}

.schlogo img {
  width: 90px;
  height: 90px;
  object-fit: cover;
  vertical-align: baseline;
  margin-left: -10px;
}

.Header-container header div .dropicon {
  display: none;
}

.Header-container header div .dropicon span {
  color: #333;
  cursor: pointer;
  font-size: 25px;
}

.side-menu {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 250px;
  height: 100%;
  background-color: white;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.5);
  transform: translateX(-100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
}

.side-menu.open {
  display: block;
  transform: translateX(0);
}

.side-menu ul {
  justify-content: center;
  list-style: none;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.side-menu ul li {
  padding: 1rem 0;
}

.side-menu ul li a {
  font-size: 16px;
  color: #333;
  text-decoration: none;
}

.side-menu ul li a:hover {
  text-decoration: underline;
  color: navy;
}

.main-nav {
  display: none;
}

@media (min-width: 900px) {
  .side-menu {
    display: none;
  }

  .main-nav {
    display: inline-block;
  }

  .Header-container header div .dropicon {
    display: none;
  }

  .Header-container header div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 1.5rem;
    padding-right: 1.5rem;
  }
}

@media (max-width: 900px) {
  .main-nav {
    display: none;
  }

  .Header-container header div .dropicon {
    display: block;
  }

  .Header-container header div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 0.3rem;
    padding-right: 0.5rem;
  }
}
